.slider {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  position: relative;
}

.slider .js-slick, .slider .slick-list {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
}

.slider *:focus {
  outline: none;
}

.slider .slick-track {
  flex: 1 1 auto;
}

.slider__slide {
  height: 100vh;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.slider__slide img {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
}

.slider__next {
  position: absolute;
  z-index: 5;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  background: white;
  border-radius: 50%;
  size: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.25s ease-in-out;
}

.slider__next:hover {
  transform: translateX(-50%) scale(1.2);
}

.is-mobile .slider__slide {
  height: calc(100vh - 55px);
}

@media only screen and (max-width: 568px) {
  .slider__next {
    size: 80px;
  }
}

.slick-next, .slick-prev {
  position: absolute;
  top: 50%;
  z-index: 5;
  -webkit-apperance: none;
  background: transparent;
  font-size: 0;
  border: 0;
  box-shadow: none;
  cursor: pointer;
  margin-top: -50px;
  transform: translateY(-50%) scale(0);
}

.slick-next::before, .slick-prev::before {
  display: inline-block;
  content: ' ';
  size: 60px;
  background: white svg-load("../img/svg/arrow.svg") no-repeat center;
  border-radius: 50%;
  background-size: 32px 12px;
  cursor: pointer;
  transition: transform 0.25s ease-in-out;
}

.slick-next {
  right: 10px;
}

.slick-next::before {
  transform: rotate(-90deg);
}

.slick-next:hover::before {
  transform: scale(1.3) rotate(-90deg);
}

.slick-prev {
  left: 10px;
}

.slick-prev::before {
  transform: rotate(90deg);
}

.slick-prev:hover::before {
  transform: scale(1.3) rotate(90deg);
}

[data-section-name='cta'] {
  min-height: 100vh;
}

.cta {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  position: relative;
}

.cta .next-section {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: -100px;
}

.cta .next-section .ico {
  transform: rotate(180deg);
}

.cta .next-section:hover .ico {
  transform: rotate(180deg) scale(1.2);
}

.cta__wrapper {
  margin-top: auto;
  margin-bottom: auto;
  flex: 0 1 auto;
  display: flex;
  position: relative;
}

.cta__text {
  position: relative;
  z-index: 5;
  flex: 0 1 50%;
  font-size: 30px;
  padding-right: 20px;
  line-height: 1.3;
  font-weight: 400;
  opacity: 0;
  transition: opacity 1s 1.6s ease;
}

.cta__text p {
  max-width: 430px;
}

.cta__text span {
  font-weight: 700;
  color: #00a99d;
}

.cta__right {
  position: relative;
  z-index: 5;
  flex: 0 0 50%;
  display: flex;
  align-items: flex-end;
  padding-left: 20px;
}

.cta__figures {
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
}

.cta__figure {
  margin-bottom: 35px;
}

.cta__figure--purple .cta__figure__number {
  color: #702c7c;
}

.cta__figure--tint .cta__figure__number {
  color: #00a99d;
}

.cta__figure--red .cta__figure__number {
  color: #f05b83;
}

.cta__figure__number {
  font-size: 86px;
  line-height: 1;
  font-weight: 900;
  opacity: 0;
  transform: translate3d(40px, 0, 0);
  transition: transform .5s 1s ease-out, opacity .3s 1.15s ease-out;
}

.cta__figure__description {
  margin-top: 10px;
  font-size: 15px;
  font-weight: 600;
  opacity: 0;
  transition: opacity .3s 1.2s ease-in;
}

.cta__call-modal {
  position: relative;
  flex: 0 0 235px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-left: auto;
  size: 235px;
  color: white;
  font-size: 30px;
  font-weight: 900;
  cursor: pointer;
  transform: scale(0);
  transition: transform .4s 1s ease, opacity .3s 1s ease;
}

.cta__call-modal span {
  display: inline-block;
  position: relative;
  z-index: 2;
  transition: transform 0.25s ease-in-out, opacity 0.15s 0.1s ease;
}

.cta__call-modal .ico-close {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 10;
  transform: translate(-50%, -50%) scale(0);
  opacity: 0;
  transition: transform 0.25s ease-in-out, opacity 0.15s 0.1s ease;
  cursor: pointer;
  pointer-events: none;
}

.cta__call-modal::before, .cta__call-modal::after {
  display: inline-block;
  content: ' ';
  position: absolute;
  size: 235px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #95ca6e;
  border-radius: 50%;
}

.cta__call-modal::before {
  opacity: 0;
  pointer-events: none;
  transition: transform .5s .1s ease-in;
  z-index: 1;
}

.cta__call-modal::after {
  z-index: 1;
  background: #95ca6e;
  border-radius: 50%;
  animation-name: ctaPulse;
  animation-duration: 2000ms;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
}

.cta__call-modal:hover::after {
  animation-play-state: paused;
}

.cta__call-modal.active span {
  opacity: 0;
  transform: scale(0);
}

.cta__call-modal.active .ico-close {
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
  pointer-events: all;
}

.cta__call-modal.active .ico-close:hover {
  transform: translate(-50%, -50%) scale(1.1) rotate(-45deg);
}

.cta__call-modal.active::before {
  opacity: 1;
  transform: translate(-50%, -50%) scale(15);
}

.cta__blot {
  position: absolute;
  background: #f2f2f2;
  border-radius: 50%;
  transform: scale(0);
}

.cta__blot--top {
  size: 135px;
  top: -20px;
  left: 50%;
  margin-left: -67px;
}

.cta__blot--middle {
  size: 32px;
  left: 63%;
  top: 37%;
}

.cta__blot--bottom {
  size: 320px;
  left: 25%;
  bottom: 0;
  transform: scale(0) translateY(250px);
}

@media only screen and (max-width: 992px) {
  .cta__text {
    flex: 0 1 380px;
    font-size: 25px;
  }
  .cta__right {
    flex: 0 0 auto;
    margin-left: auto;
  }
  .cta__figure__number {
    font-size: 75px;
  }
  .cta__call-modal {
    flex: 0 0 210px;
    size: 210px;
    font-size: 28px;
  }
  .cta__call-modal::before, .cta__call-modal::after {
    size: 210px;
  }
}

@media only screen and (max-width: 870px) {
  .cta__text {
    flex: 0 1 350px;
    font-size: 22px;
  }
  .cta__figure {
    margin-bottom: 25px;
  }
  .cta__figure__number {
    font-size: 68px;
  }
  .cta__call-modal {
    flex: 0 0 190px;
    size: 190px;
    font-size: 26px;
  }
  .cta__call-modal::before, .cta__call-modal::after {
    size: 190px;
  }
}

@media only screen and (max-width: 768px) {
  .cta__wrapper {
    flex-direction: column;
    max-width: 450px;
    margin-left: auto;
    margin-right: auto;
  }
  .cta__text {
    flex: 0 0 auto;
    padding-right: 0;
  }
  .cta__right {
    margin-top: 20px;
    flex: 0 0 auto;
    padding-left: 0;
    margin-left: 0;
    align-items: center;
  }
}

@media only screen and (max-width: 414px) {
  .cta__text {
    font-size: 20px;
  }
  .cta__right {
    flex-direction: column;
    align-items: flex-start;
  }
  .cta__call-modal {
    margin-left: auto;
    margin-right: auto;
  }
}

