.preloader {
  display: flex;
  position: fixed 0;
  z-index: 9;
  pointer-events: none;
}

.preloader__wrapper {
  position: relative;
  height: 100vh;
  width: 100%;
}

.preloader__blot {
  display: block;
  content: ' ';
  position: absolute;
  top: calc(50% - 40px);
  left: 50%;
  size: 30px;
  background: #f2f2f2;
  border-radius: 50%;
  z-index: 5;
  transform: translate(-50%, -50%) scale(1);
  animation: preloaderBlot .5s ease;
  animation-fill-mode: forwards;
}

.preloader__image {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  opacity: 0;
  transform: translate3d(-50%, -50%, 0);
  z-index: 6;
  -webkit-clip-path: circle(0px at center);
  clip-path: circle(0px at center);
}

.preloader__image img {
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
}

.preloader__circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0) scale(1);
  z-index: 7;
  opacity: 0;
  border-radius: 50%;
  size: 60px;
  background: white;
  transition: transform .3s linear;
}

.preloader.stage2 .preloader__image {
  opacity: 1;
  animation: preloaderImage .6s linear;
}

.preloader.stage2 .preloader__circle {
  transform: translate3d(-50%, -50%, 0) scale(0.5);
  opacity: 1;
  animation: preloaderCircle .5s .3s linear;
}
